.header {
  z-index: 1;
  background: #000;
  border-radius: 20px;
  position: fixed;
  top: 20px;
  box-shadow: 0 4px 10px #000;
}

.header__list {
  width: 660px;
  gap: 60px;
  display: flex;
}

.header__item {
  list-style: none;
}

.header__link {
  color: #fff;
  padding: 5px;
  font-size: 24px;
  font-weight: bolder;
  text-decoration: none;
}

.header__link:hover, .header__link:active {
  color: #000;
  background-color: #fff;
  border-radius: 20px;
}

.home {
  flex-direction: column;
  align-items: center;
  margin-top: 90px;
  display: flex;
}

.home__header {
  font-size: 40px;
  font-weight: bolder;
}

.home__avatar {
  width: 100px;
  border: 3px solid #cebdbd;
  border-radius: 50%;
}

.home__description {
  text-align: center;
  margin-right: 10px;
  padding-left: 10px;
  font-size: 20px;
  font-weight: bolder;
}

.about {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.about__header {
  font-size: 40px;
  font-weight: bolder;
}

.about__description {
  text-align: center;
  margin-top: 0;
  margin-right: 10px;
  padding-left: 10px;
  line-height: 1.5;
}

.projects {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.projects__header {
  font-size: 40px;
  font-weight: bolder;
}

.slider {
  width: 700px;
  height: 400px;
  position: relative;
  overflow: hidden;
}

.slider-img {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.slider-img.active {
  opacity: 1;
}

.slider img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider .prev {
  left: 10px;
}

.slider .next {
  right: 10px;
}

.skills {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  display: flex;
}

.skills__header {
  font-size: 40px;
  font-weight: bolder;
}

.skills__list {
  width: 700px;
  flex-flow: wrap;
  justify-content: center;
  gap: 10px;
  margin: 0;
  padding: 0;
  display: flex;
}

.skills__item {
  background-color: #000;
  border-radius: 10px;
  padding: 10px;
  font-size: 20px;
  list-style: none;
}

.contact {
  margin-bottom: 62px;
}

.contact__header {
  justify-content: center;
  margin-top: 62px;
  font-size: 40px;
  font-weight: bolder;
  display: flex;
}

.contact__container {
  justify-content: start;
  display: flex;
}

.contact__list {
  width: 200px;
  flex-flow: column wrap;
  gap: 10px;
  margin: 0;
  padding: 0;
  display: flex;
}

.contact__item {
  background-color: #000;
  border: 3px solid #000;
  border-radius: 10px;
  padding: 10px;
  font-size: 20px;
  font-weight: bolder;
  list-style: none;
}

.contact__item:hover, .contact__item:active {
  border: 3px solid #fff;
}

.contact__container {
  justify-content: space-around;
  display: flex;
}

.contact__link {
  color: #fff;
  justify-content: space-between;
  align-items: center;
  font-size: 25px;
  text-decoration: none;
  display: flex;
}

body {
  box-sizing: border-box;
  color: #fff;
  width: 700px;
  background-color: #090c27;
  margin: 0 auto;
  padding: 0;
}

